/* For contact footer and page */
.contacts {

    display: inline-block; 
    width: 80px;
    margin:10px;

}

.contactImage{
    height: 50px;
    width: 50px;
}

/* Education Page */

@media screen and (min-width: 1450px){
    #collegeImageContainer{
        text-align: center;
        width: 50%;
        float: left;
    }

    
    .collegeImage{
        margin-top: 40px;
        height:300px;
    }

    .collegeText{
        
        display: inline-block;
        width: 50%;
        padding-right: 400px;
    }
}


@media screen and (max-width: 1450px){
    #collegeImageContainer{
        text-align: center;
        width: 50%;
    }
        
    .collegeImage{
        padding: 20px;
        height:200px;
    }

    .collegeText{
        display: inline-block;
        width: 50%;
    }
}

.educationOther{
    text-align : center; 
    color: white;
    display:inline-block;
    padding-right: 50px;
}

.leftTitle{
    float: left;
    margin-left: 10px;
}
